import React, { useEffect, useState } from "react";
import { Nav, NavDropdown } from "react-bootstrap";
import { LinkContainer, IndexLinkContainer } from "react-router-bootstrap";
import sltmobitel_logo from "../pages/login/login-footer-icon.png";
import user_logo from "../pages/login/user2-160x160.jpg";
import { BrowserRouter as Link } from "react-router-dom";
import axios from "axios";

export default function SideNav(props) {
  let [approveCount, setApproveCount] = useState(0);
  let [allCount, setAllCount] = useState(0);
  let [handoverCount, setHandoverCount] = useState(0);
  let [subconCount, setSubconCount] = useState(0);
  let [acceptCount, setAcceptCount] = useState(0);
  let [workcomCount, setworkcomCount] = useState(0);
  let [workcompleteCount, setworkcompleteCount] = useState(0);
  let [boqCount, setboqCount] = useState(0);
  let [verifyBoqCount, setverifyBoqCount] = useState(0);
  let [approveBoqCount, setapproveBoqCount] = useState(0);
  let [raisePrCount, setraisePrCount] = useState(0);
  let [dropdown, setDropdwon] = useState(false);

  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  });
  useEffect(() => {
    getData();
  });

  const getData = () => {
    axiosInstance
      .get("projonline/allhandoverchanges/sideNav", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        // setHandoverCount(res.data.length)
        setAllCount(res.data.setAllCount);
      })
      .catch((err) => {
        console.log(err);
      });

    axiosInstance
      .get("projonline/gettasks/sideNav", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        setSubconCount(res.data.tasks.setSubconCount);
        setAcceptCount(res.data.tasks.setAcceptCount);
        setworkcomCount(res.data.tasks.setworkcomCount);
        setworkcompleteCount(res.data.tasks.setworkcompleteCount);
        setboqCount(res.data.tasks.setboqCount);
        setverifyBoqCount(res.data.tasks.setverifyBoqCount);
        setraisePrCount(res.data.tasks.setraisePrCount);
        setapproveBoqCount(res.data.tasks.setapproveBoqCount);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  return (
    <div>
      {/* Main Sidebar Container */}
      <aside className="main-sidebar sidebar-dark-primary elevation-4">
        {/* Brand Logo */}
        <a href="/dashboard" className="brand-link" style={{ height: "65px" }}>
          <img
            src={sltmobitel_logo}
            alt=""
            style={{
              height: "65px",
              width: "250px",
              position: "absolute",
              right: "0px",
              top: "0",
              objectFit: "cover",
            }}
          />
          {/* <span className="brand-text font-weight-light">AdminLTE 3</span> */}
        </a>
        {/* Sidebar */}
        <div className="sidebar">
          {/* Sidebar user panel (optional) */}
          <div className="user-panel mt-3 pb-3 mb-3 d-flex">
            <div className="info">
              <a href="#" className="d-block">
                Hi, {JSON.parse(localStorage.getItem("user")).name}!
              </a>
            </div>
          </div>
          {/* SidebarSearch Form */}

          {/* Sidebar Menu */}
          <nav className="mt-2">
            <ul
              className="nav nav-pills nav-sidebar flex-column nav-child-indent"
              data-widget="treeview"
              role="menu"
              data-accordion="false"
            >
              {/* Add icons to the links using the .nav-icon class
                  with font-awesome or any other icon font library */}
              <li className="nav-item menu-close">
                <Nav.Link>
                  <i className="nav-icon fas fa-user-tie" />
                  <p>
                    My Account
                    <i className="right fas fa-angle-left" />
                  </p>
                </Nav.Link>

                <ul className="nav nav-treeview">
                  <Nav.Item as="li">
                    <IndexLinkContainer to="/dashboard">
                      <Nav.Link>
                        <i className="fas fa-tachometer-alt nav-icon" />
                        <p>Dashboard</p>
                      </Nav.Link>
                    </IndexLinkContainer>
                  </Nav.Item>
                  <Nav.Item as="li">
                    <IndexLinkContainer to="/UpdateProjectTemplate">
                      <Nav.Link>
                        <i className="fas fa-tasks nav-icon" />
                        <p>Add/Update Project Template</p>
                      </Nav.Link>
                    </IndexLinkContainer>
                  </Nav.Item>
                  <Nav.Item as="li">
                    <IndexLinkContainer to="/passwordReset">
                      <Nav.Link>
                        <i className="far fa-edit nav-icon" />
                        <p>Change Password</p>
                      </Nav.Link>
                    </IndexLinkContainer>
                  </Nav.Item>
                </ul>
              </li>
              <li className="nav-item">
                <LinkContainer to="/allTasks">
                  <Nav.Link>
                    <i className="nav-icon fas fa-tasks" />
                    <p>All Tasks</p>
                  </Nav.Link>
                </LinkContainer>
              </li>

              <li className="nav-item">
                <LinkContainer to="/handoverNewTasks">
                  <Nav.Link>
                    <i className="nav-icon fas fa-handshake" />
                    <p>Handover New Tasks</p>
                  </Nav.Link>
                </LinkContainer>
              </li>
              <li className="nav-item menu-close">
                <Nav.Link>
                  <i class="nav-icon  fas fa-code-branch"></i>
                  <p>
                    Change Handover
                    <i className="right fas fa-angle-left" />
                  </p>
                </Nav.Link>
                <ul className="nav nav-treeview">
                  <Nav.Item as="li">
                    <IndexLinkContainer to="/requesthandoverchanges">
                      <Nav.Link>
                        <i className="nav-icon fas fa-envelope-open-text"></i>
                        <p>Request Handover Changes</p>
                      </Nav.Link>
                    </IndexLinkContainer>
                  </Nav.Item>

                  <Nav.Item as="li">
                    <IndexLinkContainer to="/approvehandoverchanges">
                      <Nav.Link>
                        <i className="nav-icon fas fa-check-square" />
                        {approveCount != 0 ? (
                          <span className="badge badge-danger navbar-badge">
                            {approveCount}
                          </span>
                        ) : null}
                        <p>Approve Handover Changes</p>
                      </Nav.Link>
                    </IndexLinkContainer>
                  </Nav.Item>

                  <Nav.Item as="li">
                    <IndexLinkContainer to="/allapprovedchanges">
                      <Nav.Link>
                        <i className="nav-icon fas fa-check-square" />
                        {allCount != 0 ? (
                          <span className="badge badge-danger navbar-badge">
                            {allCount}
                          </span>
                        ) : null}
                        <p>All Approved Changes</p>
                      </Nav.Link>
                    </IndexLinkContainer>
                  </Nav.Item>
                </ul>
              </li>
            </ul>
          </nav>
        </div>
      </aside>
    </div>
  );
}
