import React, { useEffect, useState } from 'react'
import { Nav } from 'react-bootstrap';
import { LinkContainer, IndexLinkContainer } from 'react-router-bootstrap';
import sltmobitel_logo from '../pages/login/login-footer-icon.png'
import user_logo from '../pages/login/user2-160x160.jpg'
import { BrowserRouter as Link } from 'react-router-dom';
import axios from 'axios';


export default function SideNav() {
  let [handoverCount, setHandoverCount] = useState(0)
  let [approveCount, setApproveCount] = useState(0)
  let [approvedCount, setApprovedCount] = useState(0)
  let [allCount, setAllCount] = useState(0)
  let [acceptCount, setAcceptCount] = useState(0)
  let [workcomCount, setworkcomCount] = useState(0)
  let [workcompleteCount, setworkcompleteCount] = useState(0)
  let [boqCount, setboqCount] = useState(0)
  let [completedTaskCount,setcompletedTaskCount] = useState(0)
  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  })
  useEffect(() => {
    getData()
  })
  const getData = () => {
    axiosInstance.get('projonline/allhandoverchanges/sideNav',{headers:{token:"Bearer "+ JSON.parse(localStorage.getItem("user")).accessToken}}).then(res => {
      setApproveCount(res.data.setApproveCount)
      setApprovedCount(res.data.setApprovedCount)
      setAllCount(res.data.setAllCount)
      // console.log(res.data.length)
    }).catch(err => {
      console.log(err)
    })
    axiosInstance.get('projonline/gettasks/sideNav', { headers: { token: "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken } }).then(res => {
      setAcceptCount(res.data.tasks.setAcceptCount)
      setworkcomCount(res.data.tasks.setworkcomCount)
      setworkcompleteCount(res.data.tasks.setworkcompleteCount)
      setboqCount(res.data.tasks.setboqCount)
      setcompletedTaskCount(res.data.tasks.setcompletedTaskCount)    

    }).catch(err => {
      console.log(err)
    })

  };


  return (
    <div>
      {/* Main Sidebar Container */}
      <aside className="main-sidebar sidebar-dark-primary elevation-4">
        {/* Brand Logo */}
        <a href="/dashboard" className="brand-link" style={{ height: '65px' }}>
          <img src={sltmobitel_logo} alt="" style={{ height: '65px', width: '250px', position: 'absolute', right: '0px', top: '0', objectFit: 'cover' }} />
          {/* <span className="brand-text font-weight-light">AdminLTE 3</span> */}
        </a>
        {/* Sidebar */}
        <div className="sidebar">
          {/* Sidebar user panel (optional) */}
          <div className="user-panel mt-3 pb-3 mb-3 d-flex">

            <div className="info">
              <a href="#" className="d-block">Hi, {JSON.parse(localStorage.getItem("user")).name}!</a>
            </div>
          </div>
          {/* SidebarSearch Form */}

          {/* Sidebar Menu */}
          <nav className="mt-2">
            <ul className="nav nav-pills nav-sidebar flex-column nav-child-indent" data-widget="treeview" role="menu" data-accordion="false">
              {/* Add icons to the links using the .nav-icon class
                  with font-awesome or any other icon font library */}
              <li className="nav-item menu-close">
                <Nav.Link >
                  <i className="nav-icon fas fa-user-tie" />
                  <p>
                    My Account
                    <i className="right fas fa-angle-left" />
                  </p>
                </Nav.Link>
                <ul className="nav nav-treeview">
                  <Nav.Item as="li">
                    <IndexLinkContainer to="/dashboard">
                      <Nav.Link >
                        <i className="fas fa-tachometer-alt nav-icon" />
                        <p>Dashboard</p>
                      </Nav.Link>
                    </IndexLinkContainer >
                  </Nav.Item>
                  <Nav.Item as="li">
                    <IndexLinkContainer to="/passwordReset">
                      <Nav.Link >
                        <i className="far fa-edit nav-icon" />
                        <p>Change Password</p>
                      </Nav.Link>
                    </IndexLinkContainer >
                  </Nav.Item>
                </ul>
              </li>
              <li className="nav-item">
                <LinkContainer to="allTasks">
                  <Nav.Link >
                    <i className="nav-icon fas fa-tasks" />
                    <p>
                      All Tasks
                    </p>
                  </Nav.Link>
                </LinkContainer>
              </li>
              <li className="nav-item menu-close">
                <Nav.Link >
                  <i class="nav-icon  fas fa-code-branch"></i>
                  <p>
                    Change Handover
                    <i className="right fas fa-angle-left" />
                  </p>
                </Nav.Link>
                <ul className="nav nav-treeview">

                  <Nav.Item as="li">
                    <IndexLinkContainer to="/approvehandoverchanges">
                      <Nav.Link >
                        <i className="nav-icon fas fa-check-square" />
                        {approveCount != 0 ? <span className="badge badge-danger navbar-badge">{approveCount}</span> : null}
                        <p>Approve Handover Changes</p>
                      </Nav.Link>
                    </IndexLinkContainer >
                  </Nav.Item>  

                  <Nav.Item as="li">
                    <IndexLinkContainer to="/approvedhandoverchanges">
                      <Nav.Link >
                        <i className="nav-icon fas fa-check-square" />
                        {approvedCount != 0 ? <span className="badge badge-danger navbar-badge">{approvedCount}</span> : null}
                        <p>Recently Approved Changes</p>
                      </Nav.Link>
                    </IndexLinkContainer >
                  </Nav.Item> 

                  <Nav.Item as="li">
                    <IndexLinkContainer to="/allapprovedchanges">
                      <Nav.Link >
                        <i className="nav-icon fas fa-check-square" />
                        {allCount != 0 ? <span className="badge badge-danger navbar-badge">{allCount}</span> : null}
                        <p>All Approved Changes</p>
                      </Nav.Link>
                    </IndexLinkContainer >
                  </Nav.Item>  
                  </ul>
                  </li> 
              
              <li className="nav-item">
                <LinkContainer to="acceptNewTasks">
                  <Nav.Link >
                    <i className="nav-icon fas fa-play" />
                    {acceptCount != 0 ? <span className="badge badge-danger navbar-badge">{acceptCount}</span> : null}
                    <p>
                      Accept New Tasks
                    </p>
                  </Nav.Link>
                </LinkContainer>
              </li>
              <li className="nav-item">
                <LinkContainer to="commenceNewTasks">
                  <Nav.Link >
                    <i className="nav-icon fas fa-play" />
                    {workcomCount != 0 ? <span className="badge badge-danger navbar-badge">{workcomCount}</span> : null}
                    <p>
                      Trigger Work Commencement
                    </p>
                  </Nav.Link>
                </LinkContainer>
              </li>
              <li className="nav-item">
                <LinkContainer to="completeNewTasks">
                  <Nav.Link >
                    <i className="nav-icon fas fa-flag-checkered" />
                    {workcompleteCount != 0 ? <span className="badge badge-danger navbar-badge">{workcompleteCount}</span> : null}
                    <p>
                      Trigger Work Completion
                    </p></Nav.Link>
                </LinkContainer>
              </li>

              <li className="nav-item">
                <LinkContainer to="submitBoqTasks">
                  <Nav.Link >
                    <i className="nav-icon fas fa-flag-checkered" />
                    {boqCount != 0 ? <span className="badge badge-danger navbar-badge">{boqCount}</span> : null}
                    <p>
                      Submit BOQ
                    </p></Nav.Link>
                </LinkContainer>
              </li>
              <li className="nav-item">
                <LinkContainer to="/completedTasks">
                  <Nav.Link >
                    {/* <i className="nav-icon fas fa-check-square" /> */}
                    <i className="nav-icon fas fa-check" />
                    {completedTaskCount != 0 ? <span className="badge badge-danger navbar-badge">{completedTaskCount}</span> : null}
                    <p>
                      Completed Task
                    </p>
                  </Nav.Link>
                </LinkContainer>
              </li>
            </ul>
          </nav>
          {/* /.sidebar-menu */}
        </div>
        {/* /.sidebar */}
      </aside>

    </div>
  )
}
