import axios from "axios";
import { loginFailure, loginStart, loginSuccess, logoutSuccess } from "./AuthActions";
import React, {useState}  from 'react'
import OtpInput from 'react-otp-input-rc-17';



export const login =async (user, dispatch, check) =>{

    const axiosInstance = axios.create({
        baseURL: process.env.REACT_APP_BACKEND_URL,
    })

  const smsFetch = async (mobileNo) => {
      const rmNo = Math.floor(Math.random()*90000) + 10000
      //setRandomNumber(rmNo)
      const url = "https://msmsenterpriseapi.mobitel.lk/EnterpriseSMSV3/esmsproxyURL.php"
      const Body ={
          "username" : "smsdigisvc_sci", 
          "password" : "Sci@#25", 
          "from": "Mobitel-PMS", 
          "to": "0"+mobileNo.toString(), 
          "text": rmNo,
          "mesageType":0
      }
      //console.log(JSON.parse(localStorage.getItem("user")))
      const requestOptions = {
          method: 'POST',
          body: JSON.stringify(Body),
          mode: 'no-cors'
      };
      try{
        const response = await fetch(url,requestOptions);
        return (rmNo)
      }catch(err){
        console.log(err)
      }
      
      

  };


    dispatch(loginStart());
    const res = {}
    try{
        //console.log("before")
        const res = await axiosInstance.post("projonline/login", user)
        localStorage.setItem("userMobileNo", JSON.stringify(res.data.mobileNo))
        //console.log(res)
        //smsFetch(res.data.mobileNo)
        if(res.data==="Wrong password or username!"){
          dispatch(loginFailure())
        }else{
          if(check) {
            dispatch(loginSuccess(res.data))
            window.location.reload(false);
          }else{
            //console.log(smsFetch(res.data.mobileNo).then(value => console.log(value)))
            return(smsFetch(res.data.mobileNo))
          }
        }
        
    }catch(err){
        console.log(err)
        dispatch(loginFailure())
    }
}

export const logout =async (dispatch) =>{
    //console.log("dd")
    const axiosInstance = axios.create({
        baseURL: process.env.REACT_APP_BACKEND_URL,
    })
    try{
        const res = await axiosInstance.get("projonline/logout", {headers:{token:"Bearer "+ JSON.parse(localStorage.getItem("user")).accessToken}})
        window.location.reload(false);
        dispatch(logoutSuccess());
    }catch(err){
        dispatch(err)
    }
}