import React, { useEffect, useState } from "react";
import { Redirect, withRouter } from "react-router-dom";
import Header from "../../components/Header";
import Footer from "../../components/Footer";
import SideNav from "../../components/SideNav";
import SideNavAdmin from "../../components/SideNavAdmin";
import SideNavSubcon from "../../components/SideNavSubcon";
import SideNavProjectTO from "../../components/SideNavProjectTO";
import AppContent from "../../components/AppContent";
import SideNavPlanning from "../../components/SideNavPlanning";
import SideNavVender from "../../components/SideNavVender";

function Dashboard(props) {
  const [visbilityBasedOn, setVisbilityBasedOn] = useState(false);

  useEffect(() => {
    document.body.classList.remove("login-page");
    setVisbilityBasedOn(
      JSON.parse(localStorage.getItem("user")).visbilityBasedOn
    );
    //window.location.reload(false);
  }, []);

  if (false) {
    return <Redirect to="/projonline/login" />;
  }

  function renderSwitch(visbilityBasedOn) {
    switch (visbilityBasedOn) {
      case "Admin":
        return <SideNavAdmin />;
        break;
      case "Planning":
        return <SideNavPlanning />;
        break;
      case "Subcon":
        return <SideNavSubcon />;
        break;
      case "Project_Div_Head":
        return <SideNavAdmin />;
        break;
      case "Project_TO":
        return <SideNavProjectTO />;
        break;
      case "Project_PM":
        return <SideNavAdmin />;
        break;
      case "Project_Coor":
        return <SideNavAdmin />;
        break;
      case "Vender":
        return <SideNavVender />;
        break;
    }
  }

  return (
    <div className="wrapper">
      <Header />
      {renderSwitch(visbilityBasedOn)}
      <AppContent />
      <Footer />
    </div>
  );
}

export default Dashboard;
